.light-div9 {
    position: absolute;
    background: linear-gradient(99.7deg, #c6fca6 0.38%, rgba(167, 252, 238, 0.74) 98.77%);
    width: 150px;
    height: 70px;
    z-index: 25;
    opacity: 49%;
    filter: blur(40px);
    border-radius: 25px;
    bottom: -50px;
    left: 100px;
}

#review-slider-div{
    opacity: 0.5;
    filter:blur(0.8px);
    transition: all 0.3s ease-in-out;
    -webkit-filter:blur(0.8px);
    -webkit-transition: all 0.3s ease-in-out;
    -moz-transition: all 0.3s ease-in-out;
    -ms-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
}
.center .slick-center #review-slider-div {
    opacity: 1;
    filter: blur(0);
    -webkit-filter: blur(0);
}