.feature-card {
  background: linear-gradient(180deg, #5c7d58 -230.45%, #538078 110.22%);
}

.slider-container {
  min-height: 0;
  min-width: 0;
  padding: 20px;
}

.slick-dots {
  bottom: -50px;
}

#future-slider-div {
  margin: 10px 0;
  padding: 2% 0;
  width: 10px;
  opacity: 0.5;

  width: fit-content;
  text-align: center;
  border-radius: 10px;
  gap: 7px;
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  padding: 75px 0px;
  transform: scale(0.8);
  -webkit-transform: scale(0.8);
  -moz-transform: scale(0.8);
  -ms-transform: scale(0.8);
  -o-transform: scale(0.8);
  transition: 0.3s;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
  position: relative;
}
#future-slider-div img {
  display: block;
  margin: auto;
  margin-bottom: 20px;
}
.center .slick-center #future-slider-div {
  z-index: 999;
  background: #0b809a;
  border-radius: 10px;
  opacity: 1;
  font-weight: bold;
  transform: scale(1.08);
  -webkit-transform: scale(1.08);
  -moz-transform: scale(1.08);
  -ms-transform: scale(1.08);
  -o-transform: scale(1.08);
  background: linear-gradient(180deg, #5c7d58 -230.45%, #538078 110.22%);
}
.light-div8 {
  top: 27rem;
  left: -93rem;
  position: absolute;
  width: 1609px;
  height: 272px;
  z-index: -99;
  background: radial-gradient(
      69.09% 95.18% at 41.72% 44.56%,
      #007872 0%,
      #0b2826 100%
    )
    /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */;
  opacity: 84%;
  filter: blur(166px);
}

