  div.transbox {
    background-color: rgba(255, 255, 255, 0.4);
    -webkit-backdrop-filter: blur(4px);
    position: absolute;
    backdrop-filter: blur(4px);
    padding: 20px;
    margin: 30px;
    font-weight: bold;
    width: 300px;
    height:  100px;
    bottom: 0;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}


#protfolio-slider-div {
  margin: 10px 0;
  padding: 2% 0;
  width: 10px;
  width: fit-content;
  text-align: center;
  border-radius: 10px;
  gap: 7px;
  opacity: 0.5;
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  padding: 75px 0px;
  transform: scale(0.8);
  -webkit-transform: scale(0.8);
  -moz-transform: scale(0.8);
  -ms-transform: scale(0.8);
  -o-transform: scale(0.8);
  transition: 0.3s;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
}
#protfolio-slider-div img {
  display: block;
  margin: auto;
  margin-bottom: 20px;
}
.center .slick-center #protfolio-slider-div {
  z-index: 999;
  border-radius: 10px;
opacity: 1;
  font-weight: bold;
  transform: scale(1.08);
  -webkit-transform: scale(1.08);
  -moz-transform: scale(1.08);
  -ms-transform: scale(1.08);
  -o-transform: scale(1.08);
}
